@import url('https://fonts.googleapis.com/css?family=Open+Sans|Quicksand:500&display=swap');

body{
  padding:10px;
}

.header-title{
  font-family: 'Quicksand', sans-serif;
  font-size: 1.3em;
}

.navbar-brand{
  font-family: 'Quicksand', sans-serif;
}

.logo{
  color: #ffd972;
}

.card-img{
  border-radius: 15px;
  height: 210px;
}


.img1{
  background-color: #ffd972;
}

.img2{
  background-color: #efa7a7;
}


.img3{
  background-color: #c7eae4;

}



.description{
  z-index: 10;
  height: 84px;
  background-color:#ffffff;
  position: relative;
  top: -40px;
  width: 90%;
  margin: auto;
  border-radius: 10px;
  padding: 15px;
  -webkit-box-shadow: 0 4px 8px -6px gray;
	   -moz-box-shadow: 0 4px 8px -6px gray;
	        box-shadow: 0 4px 8px -6px gray;
}


.description-title{
  font-family: 'Quicksand', sans-serif;
  font-weight: lighter;
}


.text-muted{
  font-size: 0.7em;
}